.App {
  text-align: center;
}
body {
  padding: 0%;
  margin: 0%;
  font-family: 'poppins', sans-serif;
}

nav {
  margin: 0;
  width: 100%;
  height: 80px;
  background:  linear-gradient(to right, 	#000, #ffffff);
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
}
.logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0px;
}

.logo-container img {
  height: 100px; /* Set the height of the logo image */
  width: 100px; /* Set the width of the logo image */
}
nav a, nav button {
  margin-right: 10px; /* Add some space between the links and buttons */
  text-decoration: none; /* Remove underline from links */
  color: white; /* Set the color of the text */
}
nav button {
  border: none; /* Remove the button border */
  background-color: transparent; /* Make the button background transparent */
  cursor: pointer; /* Change cursor to pointer on hover */
}

nav a:first-child {
  margin-left: 10px; /* Reduced margin-right of the "Home" link */
}

nav a:last-child {
  margin-left: 0; /* Remove the margin-left of the "Login" link */
}
.logo-container {
    margin-right: 10px; /* Add space between the logo and the links */
}

.logo-container img {
    height: 100px; /* Set the height of the logo image */
    width: 100px; /* Set the width of the logo image */
}

.links-container {
    display: flex; /* Use flexbox layout for the links */
    gap: 10px; /* Space the links out evenly */
}

.links-container a,
.links-container button {
    text-decoration: none; /* Remove underline from links */
    color: black; /* Set the color of the text */
    margin-right: 10px; /* Add some space between the links */
}

.links-container a:first-child {
    margin-right: 20px; /* Add space after the "Power of Knowledge Blog" link */
}


.text-button{
  background: none;
  border: none;
  color: Black; /* Adjust the color as needed */
  text-decoration: none;
  font-size: x-large;
  cursor: pointer;
  padding: 0;
  margin: 0;
}
.header-image {
  width: 100%; 
  height: 100%;
  align-items: center; 
}
.header-image-container {
  position: relative;
}

.image-overlay {
  position: absolute;
  text-transform: capitalize;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
}
.image-overlay h1{
  display: block;
    font-size: 40px;
}
.image-overlay p{
  font-style: italic;
    color: transparent;
    -webkit-text-stroke: 2px #fff;
}

.write-blog-button {
  padding: 10px 20px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.7);
    color: #000;
    text-decoration: none;
    text-transform: capitalize;
}
a {
  color: black;
  text-decoration: none;
  margin: 10px;
  font-size: 25px;
  justify-content: center;
}

.createPostPage {
  width: 100%;
  height: calc(100vh - 80px);
  display: grid;
  place-items: center;
}

.cpContainer {
  width: 500px;
  height: auto;
  padding: 20px;
  background-color: black;
  border-radius: 12px;
  color: white;
  display: flex;
  flex-direction: column;
}

.cpContainer h1 {
  text-align: center;
}

.cpContainer label {
  font-size: 25px;
}
.cpContainer .inputGp {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}

.inputGp input,
.inputGp textarea {
  font-size: 18px;
  border: none;
  border-radius: 2px;
  padding: 5px;
}

.inputGp input {
  height: 40px;
}
.inputGp textarea {
  height: 150px;
}

.cpContainer button {
  margin-top: 20px;
  height: 40px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
}
input,
textarea {
  margin-top: 5px;
}

.loginPage {
  width: 100vw;
  height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loginPage p {
  font-size: 30px;
}

/* GOOGLE BUTTOn */
.login-with-google-btn {
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.3s;
  padding: 5px 16px 12px 42px;
  border: none;
  border-radius: 3px;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);
  color: #757575;
  font-size: 25px;
  font-weight: 500;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-image: url(data:image/svg+xml;base64,);
  background-color: white;
  background-repeat: no-repeat;
  background-position: 12px 11px;
}
.login-with-google-btn:hover {
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.25);
}
.login-with-google-btn:active {
  background-color: #eeeeee;
}
.login-with-google-btn:focus {
  outline: none;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 2px 4px rgba(0, 0, 0, 0.25),
    0 0 0 3px #c8dafc;
}
.login-with-google-btn:disabled {
  filter: grayscale(100%);
  background-color: #ebebeb;
  box-shadow: 0 -1px 0 rgba(0, 0, 0, 0.04), 0 1px 1px rgba(0, 0, 0, 0.25);
  cursor: not-allowed;
}

.homePage {
  width: 100%;
  min-height: calc(100vh - 80px);
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}

.homePage .post {
  width: 600px;
  height: auto;
  max-height: 600px;
  background-color: rgb(250, 250, 250);
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  margin: 20px;
  padding: 20px;
  border-radius: 15px;
}

.post .postHeader {
  display: flex;
  justify-content: center;
  width: 100%;
}

.postHeader .title {
  flex: 50%;
}
.postHeader .deletePost {
  flex: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.deletePost button {
  border: none;

  background: none;
  font-size: 30px;
  cursor: pointer;
}

.post .postTextContainer {
  word-wrap: break-word;
  height: auto;
  max-height: 400px;
  width: 100%;
  overflow: hidden;
  overflow-y: auto;
}


nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
}

.links-container {
  display: flex;
  align-items: center;
}

.links-container a {
  margin-right: 30px;
}

.right-align {
  margin-left: auto;
}
